.file-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.file-table th,
.file-table td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

.file-item {
  display: flex;
  align-items: center;
  gap: 3px;
  cursor: pointer;
  color: #333;
}

.file-item:hover {
  text-decoration: underline;
}

.file-icon {
  color: #555;
  font-size: 16px;
}

/* file-section ÎÇ¥Î∂ÄÏóêÏÑú file-tabÏù¥ ÎÑòÏπòÏßÄ ÏïäÎèÑÎ°ù ÏÑ§Ï†ï */
.file-section {
  max-width: 100%;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

/* files-tabÏù¥ Î∂ÄÎ™® ÌÅ¨Í∏∞Î•º Î≤óÏñ¥ÎÇòÏßÄ ÏïäÎèÑÎ°ù Ï†úÌïú */
.files-tab {
  width: 100%;
  max-width: inherit; /* üîπ Î∂ÄÎ™® ÏöîÏÜå(file-section) ÌÅ¨Í∏∞Ïóê ÎßûÏ∂§ */
  padding: 15px;
  box-sizing: border-box;
}


.download-btn {
  display: flex;
  align-items: center;
  gap: 6px;
  background-color: #2C3E50;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s;
  margin-top: 10px;
}

.download-btn:hover {
  background-color: #0056b3;
}
