.comment-section {
  margin-top: 10px;
  padding: 10px;
  background: #f9f9f9;
  border-radius: 8px;
}

.comment-input {
  display: flex;
  gap: 10px;
  align-items: flex-start;
  padding: 10px;
}

/* Í∏∞Ï°¥ Î≤ÑÌäº Ïä§ÌÉÄÏùº (ÏÇ¨Ïö©ÏûêÍ∞Ä Ï¢ãÏïÑÌïòÎäî Ïä§ÌÉÄÏùº) */
.comment-input button {
  padding: 8px 12px;
  background: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.comment-input button:hover {
  background: #0069d9;
}

/* textarea Ïä§ÌÉÄÏùº */
.comment-input-textarea {
  box-sizing: border-box;
  /* Ìå®Îî© Ìè¨Ìï® */
  flex: 1;
  height: 36px;
  min-height: 36px;
  /* Ï¥àÍ∏∞ ÎÜíÏù¥ 32px: Î≤ÑÌäºÍ≥º ÎèôÏùº */
  max-height: 200px;
  padding: 8px 12px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  resize: none;
  /* ÏÇ¨Ïö©ÏûêÍ∞Ä ÏßÅÏ†ë ÌÅ¨Í∏∞ Ï°∞Ï†à Î∂àÍ∞Ä */
  overflow-y: hidden;
  /* Í∏∞Î≥∏ÏùÄ Ïà®ÍπÄ, JSÏóêÏÑú autoÎ°ú Î≥ÄÍ≤Ω */
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
}

.comment-input-textarea:focus {
  border-color: #007bff;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.3);
}


.comment-list {
  list-style: none;
  padding: 0;
  margin-top: 10px;
}

.comment-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* Ï†ê 3Í∞ú Î≤ÑÌäºÏùÑ Ïò§Î•∏Ï™Ω ÎÅùÏúºÎ°ú Ï†ïÎ†¨ */
  padding: 8px;
  border-bottom: 1px solid #ddd;
  position: relative;
  /* ÎìúÎ°≠Îã§Ïö¥ Î©îÎâ¥ absolute ÏúÑÏπò */
}

.comment-header-row {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 10px;
}

.comment-profile {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.comment-content {
  flex: 1;
  margin-right: 10px;
  /* Ï†ê 3Í∞ú Î≤ÑÌäºÍ≥º Í∞ÑÍ≤© */
}


.comment-text-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 30px;
  /* ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÏùò ÎÜíÏù¥ÏôÄ ÎèôÏùºÌïòÍ≤å Í≥†Ï†ï */
}

.comment-author {
  margin: 0;
  font-weight: bold;
  font-size: 14px;
  line-height: 1;
  /* Ï§Ñ Í∞ÑÍ≤© Ï°∞Ï†à */
}

.comment-text {
  margin-top: 8px;
  margin-bottom: 5px;
  white-space: pre-wrap;
}



.comment-date {
  margin: 0;
  font-size: 10px;
  /* ÌïÑÏöîÏóê Îî∞Îùº Ï°∞Ï†ï */
  color: #777;
  line-height: 1;
}

.delete-button {
  background: none;
  border: none;
  color: red;
  cursor: pointer;
  font-size: 14px;
  margin-left: 10px;
}

.comment-menu {
  position: relative;
  cursor: pointer;
  margin-top: 30px;
}

.menu-icon {
  font-size: 18px;
  color: #A6A6A6;
  /* Ï†ê 3Í∞ú ÏÉâÏÉÅ */
}

.dropdown-menu {
  position: absolute;
  right: 0;
  top: 20px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 10;
}

.dropdown-item {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  width: 100px;
  border: none;
  background: none;
  cursor: pointer;
  margin-bottom: 12px; /* Í∞Å ÎåìÍ∏Ä Ìï≠Î™© ÏÇ¨Ïù¥Ïóê 12px Í∞ÑÍ≤© Ï∂îÍ∞Ä */
}

.dropdown-item:hover {
  background: #f1f1f1;
}

.icon {
  margin-right: 5px;
  color: #333;
}

.comment-edit-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 30px;
  padding-top: 0;
  transform: translateY(-3px);
}

.comment-edit-buttons button {
  font-size: 14px;
  font-weight: 500;
  padding: 6px 14px;
  border: 1px solid transparent;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s ease, border-color 0.2s ease;
}

.comment-edit-buttons button:hover {
  opacity: 0.9;
}

.comment-edit-buttons button.save-btn {
  background-color: #007bff;
  color: #ffffff;
}

.comment-edit-buttons button.cancel-btn {
  background-color: #6c757d;
  color: #ffffff;
}

.comment-edit-input {
  width: calc(100% - 40px);
  /* Ï†ÑÏ≤¥ ÎÑàÎπÑÏóêÏÑú Ïò§Î•∏Ï™Ω 40px ÎßåÌÅº Ïó¨Î∞± ÌôïÎ≥¥ */
  padding: 8px 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  outline: none;
  margin-bottom: 4px;
}

.comment-edit-input:focus {
  border-color: #007bff;
  box-shadow: 0 0 4px rgba(0, 123, 255, 0.3);
}