/* üîπ TaskBoard Ï†ÑÏ≤¥ Î†àÏù¥ÏïÑÏõÉ ÏÑ§Ï†ï */
.task-board-container {
  display: flex; /* ‚úÖ LeftSidebarÏôÄ TaskBoardÎ•º ÎÇòÎûÄÌûà Î∞∞Ïπò */
  height: 100vh;
  background: #f3f4f6; /* üí° Î∞∞Í≤ΩÏÉâ Ï∂îÍ∞Ä */
  height: calc(100vh - 90px); /* ÏõêÌïòÎäî Í≥ÑÏÇ∞ ÎÜíÏù¥ Ïú†ÏßÄ */

}


/* üîπ TaskBoard Ïä§ÌÉÄÏùº */
.task-board {
  flex: 1; /* ‚úÖ ÎÇ®ÏùÄ Í≥µÍ∞ÑÏùÑ Î™®Îëê Ï∞®ÏßÄ */
  padding: 20px;
  text-align: center;
  /* background-color: #c0d5efb0; */

}

.sidebar-container {
  width: 300px;
  flex-shrink: 0;
  /* border-radius: 12px; */
  padding: 10px;
}



/* üîπ Ïπ¥Îìú Î†àÏù¥ÏïÑÏõÉ (Î∞òÏùëÌòï) */
.task-board-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr)); /* üí° Ïπ¥Îìú ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  gap: 45px; /* üí° Ïπ¥Îìú Í∞Ñ Í∞ÑÍ≤© */
  margin-top: 30px;
  justify-items: center;
  padding: 0 20px;
}

/* üîπ Í∞úÎ≥Ñ Ïπ¥Îìú Ïä§ÌÉÄÏùº */
.task-card {
  background: linear-gradient(135deg, #70a0f4, #b889fa); /* üí° Í∑∏ÎùºÎîîÏñ∏Ìä∏ Ìö®Í≥º */
  padding: 20px;
  border-radius: 15px;
  box-shadow: 4px 4px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  width: 100%;
  max-width: 320px;
  text-align: left;
  color: #ffffff; /* üí° ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* üîπ Ïπ¥Îìú Hover Ìö®Í≥º */
.task-card:hover {
  transform: translateY(-5px); /* üí° Îñ†Ïò§Î•¥Îäî Ìö®Í≥º */
  box-shadow: 6px 6px 20px rgba(0, 0, 0, 0.15);
}

/* üîπ ÌîÑÎ°úÏ†ùÌä∏ Ï†úÎ™© */
.task-card h3 {
  margin: 0 0 12px;
  font-size: 20px;
  font-weight: bold;
  color: #ffffff;
}

/* üîπ ÌîÑÎ°úÏ†ùÌä∏ ÏÑ§Î™Ö */
.task-card p {
  margin: 8px 0;
  font-size: 14px;
  opacity: 0.9;
}

/* üîπ ÌîÑÎ°úÏ†ùÌä∏ ÎÇ†Ïßú Ïä§ÌÉÄÏùº */
.project-date {
  font-size: 12px;
  opacity: 0.8;
  align-self: flex-end;
}

/* üí° Î∞òÏùëÌòï Ïä§ÌÉÄÏùº */
@media (max-width: 768px) {
  .task-board-grid {
      grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}


/* üîπ Ï†úÎ™© Ïä§ÌÉÄÏùº (ÏôºÏ™Ω Ï†ïÎ†¨ + ÎëêÍ∫ºÏö¥ Í∏ÄÏî®) */
.task-board h2 {
  text-align: left; /* üí° ÏôºÏ™Ω Ï†ïÎ†¨ */
  font-weight: bold; /* üí° Í∏ÄÏî® ÎëêÍªçÍ≤å */
  font-size: 24px; /* üí° Ìè∞Ìä∏ ÌÅ¨Í∏∞ Ï°∞Ï†ï (ÏÑ†ÌÉù ÏÇ¨Ìï≠) */
  margin-bottom: 20px; /* üí° ÌïòÎã® Ïó¨Î∞± Ï∂îÍ∞Ä */
  padding-left: 30px; /* üí° ÏôºÏ™Ω Ïó¨Î∞± Ï∂îÍ∞Ä (ÏÑ†ÌÉù ÏÇ¨Ìï≠) */
  color: #333; /* üí° Í∏ÄÏûê ÏÉâÏÉÅ (ÏÑ†ÌÉù ÏÇ¨Ìï≠) */
}
