.statistics-page {
    display: flex;
    /* background: linear-gradient(135deg, #f6f6f6 30%, #d4eaff 100%); */
    background: #f3f4f6;
    /* min-height: 100vh; */
    font-family: "Poppins", sans-serif;
    /* padding: 20px; */
        gap: 15px; /* ‚úÖ sidebar-containerÏôÄ statistics-content ÏÇ¨Ïù¥ Í∞ÑÍ≤© Ï∂îÍ∞Ä */
    height: calc(100vh - 80px); /* ÏõêÌïòÎäî Í≥ÑÏÇ∞ ÎÜíÏù¥ Ïú†ÏßÄ */


  }
  .left-sidebar {
    width: 100%;
    background-color: #f3f4f6;
    padding: 10px;
    border-radius: 12px;
}
  
  .sidebar-container {
    width: 300px;
    flex-shrink: 0;
    padding: 10px;
    background-color: #f3f4f6; /* üí° TaskBoardÏùò sidebar Î∞∞Í≤ΩÏÉâ Ïú†ÏßÄ */
    box-sizing: border-box;
    border-width: 0;
    border-style: solid;
    border-color: #e5e7eb;
}
  .statistics-content {
    flex-grow: 1;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;


  }
  
  .statistics-title {
    font-size: 24px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #333;
  }
  
  .stats-card-container {
    width: 100%;
    max-width: 700px;
    /* background: rgba(255, 255, 255, 0.7); */
    backdrop-filter: blur(10px);
    padding: 20px;
    border-radius: 16px;
    
    /* box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1); */
  }
  
  .statistics-container {
    text-align: center;
  }
  
  .statistics-heading {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #444;
  }
  
  .chart-box {
    width: 80%; /* Í∏∞Ï°¥ 80%ÏóêÏÑú 100%Î°ú Ï°∞Ï†ï */
    max-width: 600px; /* Í∏∞Ï°¥ 500pxÏóêÏÑú 700pxÎ°ú Ï°∞Ï†ï */

    margin: auto;
    padding: 20px;
    background: #fff;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
        /* Ï∂îÍ∞ÄÎêú ÏΩîÎìú */
        display: flex; /* ÎÇ¥Î∂Ä ÏöîÏÜå Ï†ïÎ†¨ÏùÑ ÏúÑÌïú flex Ï†ÅÏö© */
        justify-content: center; /* Í∞ÄÎ°ú Î∞©Ìñ• Ï§ëÏïô Ï†ïÎ†¨ */
        align-items: center; /* ÏÑ∏Î°ú Î∞©Ìñ• Ï§ëÏïô Ï†ïÎ†¨ */
  }
  
  .status-list {
