/* üìÅ FilePreview.css */
.file-preview-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8); /* Î∞∞Í≤Ω Ïñ¥Îë°Í≤å */
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000; /* ÏµúÏÉÅÎã® Î∞∞Ïπò */
}

.file-preview-content {
  background: #222; /* Í≤ÄÏùÄÏÉâ Î∞∞Í≤Ω */
  padding: 20px;
  border-radius: 10px;
  max-width: 80%;
  max-height: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  color: white; /* ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ */
}

.close-button {
  position: absolute;
  top: 15px;
  right: 15px;
  background: transparent;
  border: none;
  color: white;
  font-size: 24px;
  cursor: pointer;
}

.file-info {
  background: rgba(255, 255, 255, 0.1);
  padding: 10px;
  border-radius: 5px;
  width: 100%;
  text-align: center;
  margin-top: 10px;
  font-size: 14px;
}

.preview-image {
  max-width: 400px;
  max-height: 400px;
  border-radius: 5px;
  margin-bottom: 15px;
}

.download-button {
  background: #007aff; /* Îã§Ïö¥Î°úÎìú Î≤ÑÌäº ÌååÎûÄÏÉâ */
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.download-button:hover {
  background: #005ecb;
}
