.task-line{
  background: #fff;
  border-radius: 10px;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); */
  padding: 20px;
  border: 1px solid #cdcdcd;
  position: relative; /* ÎìúÎ°≠Îã§Ïö¥ ÏúÑÏπòÎ•º ÏúÑÌïú ÏÉÅÎåÄ ÏúÑÏπò */
}
/* ÏûëÏóÖ Î™©Î°ùÍ≥º Í∞ÑÌä∏Ï∞®Ìä∏Í∞Ä Îã¥Í∏¥ Ïª®ÌÖåÏù¥ÎÑà */
.gantt-chart-tab-task-container {
  display: flex;
  flex-wrap: wrap;      /* Í≥µÍ∞Ñ Î∂ÄÏ°± Ïãú Ìï≠Î™©Îì§Ïù¥ Îã§Ïùå Ï§ÑÎ°ú ÎÇ¥Î†§Í∞ÄÎèÑÎ°ù */
  gap: 20px;
}

/* Í∞ÑÌä∏Ï∞®Ìä∏ ÌÉ≠ ÏûëÏóÖ Î™©Î°ù Ïª®ÌÖåÏù¥ÎÑà */
.gantt-chart-tab-task-list {
  background: #fff;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

/* ÌÖåÏù¥Î∏î Ï†ÑÏ≤¥ */
.gantt-chart-tab-task-list table {
  width: 100%;
  border-collapse: collapse;
}

/* ÌÖåÏù¥Î∏î Ìó§ÎçîÏôÄ ÏÖÄ Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
.gantt-chart-tab-task-list th,
.gantt-chart-tab-task-list td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #e0e0e0;
  font-size: 14px;
}

/* ÌÖåÏù¥Î∏î Ìó§Îçî Ïä§ÌÉÄÏùº */
.gantt-chart-tab-task-list th {
  background-color: #f5f5f5;
  font-weight: 600;
  color: #555;
}

/* ÌÖåÏù¥Î∏î ÏÖÄ Ïä§ÌÉÄÏùº */
.gantt-chart-tab-task-list td {
  color: #666;
}

/* ÎßàÏßÄÎßâ ÌñâÏùò ÌïòÎã® Í≤ΩÍ≥ÑÏÑ† Ï†úÍ±∞ */
.gantt-chart-tab-task-list tr:last-child td {
  border-bottom: none;
}

/* ÏßùÏàò Ìñâ Î∞∞Í≤ΩÏÉâ */
.gantt-chart-tab-task-list tr:nth-child(even) {
  background-color: #fafafa;
}

/* Ìñâ Ìò∏Î≤Ñ Ìö®Í≥º */
.gantt-chart-tab-task-list tr:hover {
  background-color: #f0f8ff;
}


/* Í∞ÑÌä∏Ï∞®Ìä∏ Ïª®ÌÖåÏù¥ÎÑà */
.gantt-chart-tab-container {
  flex: 2;              /* ÏûëÏóÖ Î™©Î°ùÎ≥¥Îã§ Îçî ÎÑìÍ≤å Î∞∞Î∂Ñ */
  min-width: 300px;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;   /* ÏàòÌèâ Ïä§ÌÅ¨Î°§ Ï†úÍ±∞ */
}

