/* Î©îÏãúÏßÄ Î™©Î°ù Ïª®ÌÖåÏù¥ÎÑà */
.messages {
  display: flex;
  flex-direction: column;
  gap: 6px; /* Î©îÏãúÏßÄ Í∞Ñ Ïó¨Î∞± (Ï°∞Í∏à Îçî Ï¢ÅÍ≤å) */
  max-height: 400px;
  overflow-y: auto;
  padding: 12px; /* Ìå®Îî© Ï§ÑÏûÑ */
  background-color: #ffffff;
  align-items: flex-start;

}
.team-chatbox-container {
