/* Î©îÏãúÏßÄ Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.message {
  display: flex;
  flex-direction: column;
  gap: 2px; /* ÎÇ¥Î∂Ä ÏöîÏÜå Í∞ÑÍ≤© ÏµúÏÜåÌôî */
  width: 60%; /* Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Îçî ÎÑìÍ≤å ÏÑ§Ï†ï */
  max-width: 60%; /* ÎÑàÎ¨¥ Í∏∏Ïñ¥ÏßÄÏßÄ ÏïäÎèÑÎ°ù Ï†úÌïú */
  padding: 6px 12px; /* ÎÇ¥Î∂Ä Ìå®Îî© Ïú†ÏßÄ */
  font-size: 14px;
  line-height: 1.2;
  word-wrap: break-word;
  word-break: break-word;
  position: relative;
  flex-shrink: 0; /* Î©îÏãúÏßÄÍ∞Ä Î∂ÄÎ™® ÏöîÏÜåÏóê ÏùòÌï¥ ÏûêÎèôÏúºÎ°ú Ï§ÑÏñ¥Îì§ÏßÄ ÏïäÎèÑÎ°ù Î∞©ÏßÄ */
}

/* ÏÉÅÎåÄÎ∞© Î©îÏãúÏßÄ Ïä§ÌÉÄÏùº */
.other-message {
  background: #e5e5ea;
  color: black;
  align-self: flex-start;
  border-radius: 18px 18px 18px 6px;
  /* padding: 8px 12px; */
  max-width: 90%; /* Í∞ÄÎ°ú Í∏∏Ïù¥ ÌôïÏû• */

}

/* ÎÇ¥ Î©îÏãúÏßÄ Ïä§ÌÉÄÏùº */
.my-message {
  background: #007aff;
  color: white;
  align-self: flex-end;
  border-radius: 18px 18px 6px 18px;
  /* padding: 8px 12px; */
  max-width: 90%; /* Í∞ÄÎ°ú Í∏∏Ïù¥ ÌôïÏû• */

}

/* Î≥¥ÎÇ∏ ÏÇ¨Îûå Ïä§ÌÉÄÏùº */
.sender {
  font-size: 12px; /* Í∏ÄÏûê ÌÅ¨Í∏∞ Ï§ÑÏù¥Í∏∞ */
  font-weight: bold;
  margin-bottom: 0px; /* ÏïÑÎûò Ïó¨Î∞± Ï†úÍ±∞ */
}

/* Î©îÏãúÏßÄ ÎÇ¥Ïö© Ïä§ÌÉÄÏùº */
.message p {
  margin: 0px; /* Î©îÏãúÏßÄÏôÄ ÌÉÄÏûÑÏä§ÌÉ¨ÌîÑ Í∞Ñ Ïó¨Î∞± Ï†úÍ±∞ */
  padding: 0px; /* ÎÇ¥Î∂Ä Ìå®Îî© Ï†úÍ±∞ */
}


/* Î©îÏãúÏßÄ Í∞ÑÍ≤© Ï°∞Ï†ï */
.message + .message {
  margin-top: 6px;
}

/* Î©îÏãúÏßÄ ÎßêÌíçÏÑ†Ïóê Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä */
.my-message,
.other-message {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

/* ÏûÖÎ†•Ï∞Ω Ïä§ÌÉÄÏùº */
.message-input {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px;
  background: #ffffff;
  border-top: 1px solid #ddd;
  overflow: hidden; /* Î≤ÑÌäºÏù¥ ÌäÄÏñ¥ÎÇòÍ∞ÄÏßÄ ÏïäÎèÑÎ°ù */
  flex-wrap: nowrap; /* ÎÇ¥Î∂Ä ÏöîÏÜåÍ∞Ä Ìïú Ï§Ñ Ïú†ÏßÄ */
}

/* ÏûÖÎ†• Ïπ∏ */
.inputtext {
  min-width: 0; /* flex ÏïÑÏù¥ÌÖúÏù¥ Î∂ÄÎ™® ÌÅ¨Í∏∞Ïóê ÎßûÏ∂∞ Ï§ÑÏñ¥Îì§ÎèÑÎ°ù ÏÑ§Ï†ï */
  flex-grow: 1;
  padding: 12px 15px;
  border: 1px solid #ccc;
  border-radius: 25px;
  font-size: 12px;
  background: #f4f7fc;
  outline: none;
  transition: 0.2s ease-in-out;
  flex-grow: 0.85; /* ÏûÖÎ†• ÌïÑÎìúÍ∞Ä ÎÑàÎ¨¥ ÌôïÏû•ÎêòÏßÄ ÏïäÎèÑÎ°ù Ï°∞Ï†ï */

}

.inputtext:focus {
  border-color: #2f80ed;
  background: #fff;
  box-shadow: 0px 0px 8px rgba(47, 128, 237, 0.3);
}

/* Ï†ÑÏÜ° Î≤ÑÌäº */
.button {
  background: #007aff;
  color: white;
  border: none;
  padding: 12px 18px;
  font-size: 10px;
  font-weight: bold;
  cursor: pointer;
  border-radius: 25px;
  transition: background 0.3s ease, transform 0.2s ease-in-out;
  justify-content: center; /* ÌÖçÏä§Ìä∏ Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  white-space: nowrap; /* ÌÖçÏä§Ìä∏Í∞Ä Ï§Ñ Î∞îÍøàÎêòÏßÄ ÏïäÎèÑÎ°ù ÏÑ§Ï†ï */
}

.button:hover {
  background: #005ecb;
  transform: scale(1.05);
}

.button:active {
  background: #004bb7;
  transform: scale(0.98);
}

/* ÌÉÄÏûÑÏä§ÌÉ¨ÌîÑÍ∞Ä Î©îÏãúÏßÄ ÌÅ¨Í∏∞Ïóê ÏòÅÌñ•ÏùÑ Ï£ºÏßÄ ÏïäÎèÑÎ°ù ÏÑ§Ï†ï */
.timestamp {
  display: block; /* Í∞úÎ≥Ñ Ï§ÑÎ°ú ÏÑ§Ï†ï */
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
  text-align: right;
  margin-top: 3px;
  width: 100%; /* Î∂ÄÎ™® ÏöîÏÜåÎ•º ÍΩâ Ï±ÑÏö∞ÎèÑÎ°ù ÏÑ§Ï†ï */
  white-space: nowrap; /* Ï§ÑÎ∞îÍøà Î∞©ÏßÄ */
}


/* Î∞òÏùëÌòï: ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑú Î©îÏãúÏßÄÍ∞Ä Îçî ÎÑìÍ≤å Î≥¥Ïù¥ÎèÑÎ°ù ÏÑ§Ï†ï */
@media (max-width: 600px) {
  .message {
    max-width: 95%; /* Î™®Î∞îÏùºÏóêÏÑúÎäî 95%ÍπåÏßÄ ÌôïÏû• */
  }
}
