/* TaskModal (ÏóÖÎ¨¥ Ï∂îÍ∞Ä Î™®Îã¨)Ïùò Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
.task-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  /* background: rgba(0, 0, 0, 0.5); */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Î∂ÄÎ™® ÏöîÏÜå ÌÅ¨Í∏∞ Ï∂ïÏÜå */
.task-modal-container {
  max-width: 480px; /* Í∏∞Ï°¥Î≥¥Îã§ ÏûëÍ≤å Ï°∞Ï†ï */
  overflow: visible !important;

}

.task-modal-content {
  background: white;
  padding: 20px; /* Ìå®Îî© Í∞êÏÜå */
  border-radius: 10px;
  width: 100%;
  max-width: 470px; /* Í∏∞Ï°¥Î≥¥Îã§ ÏûëÍ≤å Ï°∞Ï†ï */
  height: auto;
  max-height: 85vh; /* ÎÜíÏù¥ Ï∂ïÏÜå */
  overflow-y: auto;
  text-align: center;
  border: 1px solid #ccc;

}

/* ÏûÖÎ†• ÌïÑÎìú Î∞è Î≤ÑÌäº ÌÅ¨Í∏∞ Ï°∞Ï†ï */
.task-modal-content label {
  display: block;
  font-weight: bold;
  margin-top: 15px; /* Í∞ÑÍ≤© Ï°∞Ï†ï */
  text-align: left;
  font-size: 16px;
}

.task-modal-content input,
.task-modal-content select,
.task-modal-content textarea {
  width: 100%;
  padding: 10px; /* Ìå®Îî© Í∞êÏÜå */
  margin-top: 8px; /* Í∞ÑÍ≤© Ï∂ïÏÜå */
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
}

.modal-content textarea {
  height: 140px; /* Í∏∞Ï°¥Î≥¥Îã§ ÎÜíÏù¥ Ï∂ïÏÜå */
  resize: none;
}

/* Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.modal-actions {
