}

/* ÏïÑÏù¥ÏΩò Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.icon-btn {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px; /* ÌÅ¨Í∏∞ Ï∂ïÏÜå */
  font-size: 16px;
