}

/* Î™®Îã¨ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.modal-actions-right button {
  margin-left: 5px;
  position: relative;
  padding: 12px 20px; /* Î≤ÑÌäº ÌÅ¨Í∏∞ */
  font-size: 13px; /* Í∏ÄÏûê ÌÅ¨Í∏∞ */
  font-weight: bold;
  border-radius: 8px; /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
  border: 2px solid transparent; /* Ìà¨Î™Ö ÌÖåÎëêÎ¶¨ */
  background: white; /* Í∏∞Î≥∏ Î∞∞Í≤Ω */
  color: #333; /* Í∏∞Î≥∏ Í∏ÄÏûêÏÉâ */
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

/* Í∑∏ÎùºÎîîÏñ∏Ìä∏ ÌÖåÎëêÎ¶¨ Ìö®Í≥º */
.modal-actions-right button::before {
  content: "";
  position: absolute;
  inset: 0; /* Î≤ÑÌäº ÌÅ¨Í∏∞ÏôÄ ÎèôÏùº */
  border-radius: 8px; /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
  padding: 2px; /* ÌÖåÎëêÎ¶¨ ÎëêÍªò */
  background: linear-gradient(135deg, #70a0f4, #b889fa); /* Í∑∏ÎùºÎîîÏñ∏Ìä∏ ÌÖåÎëêÎ¶¨ */
  -webkit-mask: linear-gradient(white, white) content-box, linear-gradient(white, white);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
  pointer-events: none; /* ÌÅ¥Î¶≠ Î∞©Ìï¥ Î∞©ÏßÄ */
}

/* Ìò∏Î≤Ñ Ïãú Î∞∞Í≤ΩÏÉâ Î≥ÄÍ≤Ω */
.modal-actions-right button:hover {
  background: linear-gradient(135deg, #70a0f4, #b889fa); /* Í∑∏ÎùºÎîîÏñ∏Ìä∏ Î∞∞Í≤Ω */
  color: white; /* Í∏ÄÏûêÏÉâ Î≥ÄÍ≤Ω */
}


/* ÏÉâÏÉÅ ÏÑ†ÌÉù Ïª®ÌÖåÏù¥ÎÑà */
.color-picker-container {
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
  margin-top: 8px;
}

/* Ïà®ÍπÄ Ï≤òÎ¶¨Îêú Ïª¨Îü¨ ÏÑ†ÌÉù input */
.hidden-color-picker {
  position: absolute;
  top: -40px; /* ÏÉâÏÉÅ ÏÑ†ÌÉù Ï∞ΩÏùÑ ÏúÑÎ°ú Ïù¥Îèô */
  left: 0;
  width: 100%;
  opacity: 0; /* ÏôÑÏ†ÑÌûà Ïà®ÍπÄ */
  pointer-events: none; /* ÌÅ¥Î¶≠ Î∂àÍ∞ÄÎä• */
}

/* ÏÑ†ÌÉùÎêú ÏÉâÏÉÅ ÎØ∏Î¶¨Î≥¥Í∏∞ (ÌÅ¥Î¶≠ Í∞ÄÎä•) */
.color-box {
  width: 40px;
  height: 40px;
  border-radius: 6px;
  border: 2px solid #ccc;
  cursor: pointer;
  transition: transform 0.2s ease-in-out, border 0.2s ease-in-out;
}

/* Ìò∏Î≤Ñ Ïãú ÏÉâÏÉÅ Í∞ïÏ°∞ */
.color-box:hover {
  transform: scale(1.1);
  border: 2px solid #555;
}

/* ÏÑ†ÌÉùÎêú ÏÉâÏÉÅÏùò HEX ÏΩîÎìú ÌëúÏãú */
.color-code {
  font-size: 14px;
  font-weight: bold;
  color: #555;
}
