/* Î™®Îã¨ Ïª®ÌÖåÏù¥ÎÑà */
.profile-modal-content {
  background: white;
  width: 320px; /* Î™®Îã¨ ÌÅ¨Í∏∞ Ï∂ïÏÜå */
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  padding-bottom: 8px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥ */
.profile-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* Îã´Í∏∞ Î≤ÑÌäº */
.profile-modal-close-btn {
  position: absolute;
  top: 12px; /* ÏúÑÏπò Ï°∞Ï†ï */
  right: 12px;
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  color: #999;
}

.profile-modal-close-btn:hover {
  color: black;
}

/* ÌîÑÎ°úÌïÑ Ìó§Îçî */
.profile-modal-header {
  background: linear-gradient(to bottom, #c3b1e1, #ffffff);
  padding: 30px 15px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

/* ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄ */
.profile-modal-image {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid white;
  background-color: #f0f0f0;
}

/* ÏÇ¨Ïö©Ïûê Ïù¥Î¶Ñ */
.profile-modal-username {
  font-size: 20px;
  font-weight: bold;
  margin-top: 8px;
  color: #333;
}

/* ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ */
.profile-modal-info {
  padding: 10px 15px;
}

/* Ï†ïÎ≥¥ ÏïÑÏù¥ÌÖú */
.profile-modal-info-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 15px;
  color: #444;
  margin-bottom: 8px;
  padding-bottom: 4px;
  border-bottom: 1px solid #eee;
  padding: 10px;
}

/* ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ Í∞ÑÍ≤© */
.profile-modal-info-item strong {
  display: flex;
  align-items: center;
  min-width: 100px;
  color: #666;
}

/* Î≤ÑÌäº Ïª®ÌÖåÏù¥ÎÑà */
.profile-modal-actions {
  display: flex;
  justify-content: space-between;
  padding: 8px 15px;
  background: white;
  border-top: 1px solid #ddd;
}

/* Ï±ÑÌåÖ Î≤ÑÌäº */
.profile-modal-chat-btn {
  background: white;
  border: 1px solid #ccc;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
}

/* Ï†ïÎ≥¥ ÏàòÏ†ï Î≤ÑÌäº */
.profile-modal-edit-btn {
  background: #007bff;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  flex: 1;
  justify-content: center;
}

.profile-modal-edit-btn:hover {
  background: #0056b3;
}
