/* PostTodoModal.css */
.post-todo-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;

}

.post-todo-modal-container {
  background: white;
  width: 500px;
  border-radius: 8px;
  padding: 20px;
  display: flex;
  flex-direction: column;
    border: 2px solid #ccc;

}

/* üìå contentEditable Ïä§ÌÉÄÏùº */
.post-todo-editable-content {
  width: 100%;
  min-height: 150px;
  padding: 10px;
  border: 2px solid #ddd;
  border-radius: 6px;
  background: white;
  overflow-y: auto;
  font-size: 14px;
  color: #333;
  position: relative; /* ‚úÖ Î∂ÄÎ™® ÏöîÏÜåÎ•º relativeÎ°ú ÏÑ§Ï†ï */
}

/* ‚úÖ placeholder Î¨∏Íµ¨Î•º contentEditable ÎÇ¥Î∂ÄÏóê ÌëúÏãú */
.post-todo-editable-content:empty::before {
  content: attr(placeholder); /* ‚úÖ HTMLÏóêÏÑú ÏßÄÏ†ïÌïú placeholderÎ•º ÌëúÏãú */
  color: #999; /* ‚úÖ Ïó∞Ìïú ÌöåÏÉâ */
  font-size: 14px;
  position: absolute; /* ‚úÖ ÎÇ¥Î∂ÄÏóêÏÑú placeholderÍ∞Ä Îñ† ÏûàÎèÑÎ°ù */
  top: 10px;
  left: 10px;
  pointer-events: none; /* ‚úÖ ÏûÖÎ†•Ìï† Îïå placeholderÍ∞Ä ÏÑ†ÌÉùÎêòÏßÄ ÏïäÎèÑÎ°ù */
}

.post-todo-modal-content {
  display: flex;
  flex-direction: column;
  gap: 10px;

}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.modal-actions button {
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.modal-actions button:last-child {
  background: #5c6bc0;
  color: white;
}

textarea {
  width: 100%;
  height: 100px;
  resize: none;
  border: 1px solid #ccc;
  padding: 8px;
  border-radius: 4px;
}

/* Î™®Îã¨ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Ïä§ÌÉÄÏùº */
.modal-nav {
  display: flex;
  justify-content: space-between;
  background: #f5f5f5;
  padding: 8px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.modal-nav button {
