  color: white;
}
.modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
}

.modal-actions-left {
  display: flex;
  gap: 10px;
}

.icon-btn {
  background: none;
  border: none;
  font-size: 16px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  color: #666;
}

.icon-btn:hover {
  color: #000;
}
.image-container {
  position: relative;
  display: inline-block;
  margin: 10px 0;
}

.uploaded-image {
  max-width: 100%;
  border-radius: 8px;
  display: block;
}

/* ÏÇ≠Ï†ú Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.delete-image-btn {
  position: absolute;
  top: 5px;
  right: 5px;
  background: red;
  color: white;
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0; /* Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Ïà®ÍπÄ */
}

/* ÎßàÏö∞Ïä§Î•º Ïò¨Î¶¨Î©¥ ÏÇ≠Ï†ú Î≤ÑÌäº ÌëúÏãú */
.image-container:hover .delete-image-btn {
