}

.delete-file-btn {
  background: rgba(255, 0, 0, 0.0); /* üî• Î∞∞Í≤Ω ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
  color: white;
  border: none;
  border-radius: 50%;
  width: 28px; /* üî• Î≤ÑÌäº ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  height: 28px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  font-size: 16px; /* üî• ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2); /* üî• Í∑∏Î¶ºÏûê Ï∂îÍ∞Ä */
}

.file-container:hover .delete-file-btn {
