}

/* üìå ÌîºÎìúÏóêÏÑúÎäî ÏÇ≠Ï†ú Î≤ÑÌäº Ïà®Í∏∞Í∏∞ */
.activity-feed .delete-file-btn {
  display: none !important;
}

/* üìå Î™®Îã¨ÏóêÏÑúÎßå ÏÇ≠Ï†ú Î≤ÑÌäº Î≥¥Ïù¥Í∏∞ */
.modal-container .delete-file-btn {
  display: flex; /* üî• displayÎ•º flexÎ°ú Î≥ÄÍ≤ΩÌïòÏó¨ ÏïÑÏù¥ÏΩòÏù¥ Ï†ïÎ†¨ÎêòÎèÑÎ°ù */
}
.modal-actions-left {
  position: relative; /* üîπ Î∂ÄÎ™® ÏöîÏÜå Í∏∞Ï§ÄÏúºÎ°ú ÏúÑÏπò Ï°∞Ï†ï */
  display: flex;
  align-items: center; /* üîπ Î≤ÑÌäºÎì§ÏùÑ Í∞ôÏùÄ Ï§ÑÏóê Ï†ïÎ†¨ */
  gap: 8px; /* üîπ Î≤ÑÌäº Í∞ÑÍ≤© Ï∂îÍ∞Ä */
}

.file-upload-container {
  margin-top: 16px; /* üîπ 'ÌååÏùº Ï∂îÍ∞Ä' Î≤ÑÌäºÍ≥º ÌååÏùº ÏóÖÎ°úÎìú UI ÏÇ¨Ïù¥ Í∞ÑÍ≤© Ï∂îÍ∞Ä */
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 10;
}


/* Ïò§Î•∏Ï™Ω Î≤ÑÌäº ÏòÅÏó≠ Ïä§ÌÉÄÏùº */
.task-modal-actions-right {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  margin-top: 20px;
}

/* Î≤ÑÌäº Í∏∞Î≥∏ Ïä§ÌÉÄÏùº */
.task-modal-actions-right button {
  padding: 12px 22px;
  font-size: 14px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

/* Ï∑®ÏÜå Î≤ÑÌäº (ÌöåÏÉâ) */
.task-modal-actions-right button:first-child {
  background: #e2e2e2;
  color: #333;
}

.task-modal-actions-right button:first-child:hover {
  background: #b3b3b3;
}

/* Îì±Î°ù Î≤ÑÌäº */
.task-modal-actions-right button:last-child {
  background: #6A5ACD; /* iOS Ïä§ÌÉÄÏùº Î∏îÎ£® */
  color: #ffffff;
  box-shadow: 0px 4px 6px rgba(0, 122, 255, 0.2);
}

.task-modal-actions-right button:last-child:hover {
  background: #6A5ACD; /* Îçî ÏßÑÌïú Î∏îÎ£® */
  box-shadow: 0px 6px 12px rgba(0, 94, 194, 0.3);
  transform: scale(1.05);
}

.task-modal-actions-right button:last-child:active {
  background: #004799;
  transform: scale(1);
}

/* üìå Ï†úÎ™© ÏûÖÎ†• ÌïÑÎìú Ïä§ÌÉÄÏùº */
/* üìå Í∏∞Î≥∏ ÏûÖÎ†• ÌïÑÎìú Ïä§ÌÉÄÏùº (Í∏Ä ÏûëÏÑ± & ÏóÖÎ¨¥ Ï∂îÍ∞Ä Î™®Îëê Ï†ÅÏö©Îê®) */
.post-todo-modal-container input[type="text"] {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  outline: none;
  transition: border-bottom 0.3s ease;
}

/* üìå Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Î∞ëÏ§Ñ Ï†ÅÏö© (Í∏Ä ÏûëÏÑ± ÌÉ≠ÏóêÏÑú Ï†ÅÏö©Îê®) */
.post-todo-modal-container:not(.task-section) input[type="text"] {
  border-bottom: 2px solid #8b8b8b; /* ‚úÖ ÏóÖÎ¨¥ ÌÉ≠Ïù¥ ÏïÑÎãê ÎïåÎßå Ï†ÅÏö© */
}

/* üìå ÏóÖÎ¨¥(Task) ÌÉ≠ÏóêÏÑúÎäî Î∞ëÏ§Ñ Ï†úÍ±∞ */
.task-section input[type="text"] {
  border-bottom: none !important;
}
/* üìå ÏûÖÎ†• ÌïÑÎìú Ìè¨Ïª§Ïä§ Ïãú Ïä§ÌÉÄÏùº Î≥ÄÍ≤Ω */
.post-todo-modal-container input[type="text"]:focus {
  border-bottom: 2px solid #3f51b5; /* ‚úÖ Ìè¨Ïª§Ïä§ Ïãú ÏÉâÏÉÅ ÏßÑÌïòÍ≤å */
}





/* üìå ÏóÖÎ¨¥ Ï∂îÍ∞Ä Î™®Îã¨ ÌÅ¨Í∏∞ Ï°∞Ï†ï */

/* üìå ÏûÖÎ†• ÌïÑÎìú & ÏÖÄÎ†âÌä∏ Î∞ïÏä§ ÌÅ¨Í∏∞ Ï°∞Ï†ï */
.task-section input,
.task-section select {
  width: 100%;
  padding: 10px; /* üîπ ÎÇ¥Î∂Ä Ìå®Îî© Ï∂ïÏÜå */
  border-radius: 6px; /* üîπ Í∏∞Ï°¥ 8px ‚Üí 6pxÎ°ú Ï∂ïÏÜå */
  font-size: 14px;
  background: #f9f9f9;
  transition: border-color 0.3s ease;
}

.task-section input:focus,
.task-section select:focus {
  border-color: #5c6bc0;
  outline: none;
}

/* üìå ÏóÖÎ¨¥ ÏÉÅÌÉú ÎìúÎ°≠Îã§Ïö¥ Ïä§ÌÉÄÏùº */
.task-status-dropdown {
  background: #f5f5f5;
  font-weight: 500;
}

/* üìå ÏÉâÏÉÅ ÏÑ†ÌÉù ÌïÑÎìú */
.task-section input[type="color"] {
  height: 35px; /* üî• ÌÅ¨Í∏∞ Ï∂ïÏÜå */
  border: none;
  cursor: pointer;
  padding: 0;
}

/* üìå ÏûëÏóÖ ÎÇ¥Ïö© ÏûÖÎ†• ÌïÑÎìú */
.task-description {
  min-height: 100px; /* üî• Í∏∞Ï°¥ 120px ‚Üí 100pxÎ°ú Ï∂ïÏÜå */
  border: 1px solid #ddd;
  padding: 8px; /* üîπ Ìå®Îî© Ï∂ïÏÜå */
  border-radius: 6px;
  background: white;
  font-size: 14px;
  color: #333;
  overflow-y: auto;
}

/* üìå Î≤ÑÌäº ÏòÅÏó≠ */
.task-button-group {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  margin-top: 15px; /* üîπ Î≤ÑÌäº Í∞Ñ Í∞ÑÍ≤© Ï°∞Ï†à */
}

/* üìå Ï∑®ÏÜå Î≤ÑÌäº */
.task-button-group .cancel-btn {
  background: #e2e2e2;
  color: #333;
  padding: 8px 14px; /* üî• ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  border-radius: 6px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: background 0.3s;
}

.task-button-group .cancel-btn:hover {
