}

/* üìå Îì±Î°ù Î≤ÑÌäº */
.task-button-group .submit-btn {
  background: #5c6bc0;
  color: white;
  padding: 8px 14px; /* üî• ÌÅ¨Í∏∞ Ï°∞Ï†ï */
  border-radius: 6px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  transition: background 0.3s, transform 0.2s;
}

.task-button-group .submit-btn:hover {
  background: #3f51b5;
  transform: scale(1.03);
}

/* üìå Î∞òÏùëÌòï: ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑúÎèÑ ÏµúÏ†ÅÌôî */
@media (max-width: 480px) {
  .task-section {
    max-width: 90%; /* üî• ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑúÎäî Ï†ÑÏ≤¥ ÎÑàÎπÑ ÏÇ¨Ïö© */
  }

  .task-button-group {
    flex-direction: column; /* üî• Î≤ÑÌäºÏùÑ ÏÑ∏Î°úÎ°ú Ï†ïÎ†¨ */
  }

  .task-button-group button {
    width: 100%;
  }
}


/* üìå Ïû•ÏÜå Í≤ÄÏÉâ Ïª®ÌÖåÏù¥ÎÑà Ï§ëÏïô Î∞∞Ïπò */
.place-search-container {
  position: fixed; /* ‚úÖ ÌôîÎ©¥ Ï§ëÏïô Í≥†Ï†ï */
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); /* ‚úÖ Ï†ïÌôïÌïú Ï§ëÏïô Ï†ïÎ†¨ */
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
  padding: 15px;
  width: 400px; /* ‚úÖ Ï†ÅÏ†àÌïú ÌÅ¨Í∏∞ ÏÑ§Ï†ï */
  max-width: 90%;
  z-index: 1050;
  overflow: hidden;
}

/* üìå Ïû•ÏÜå Í≤ÄÏÉâ ÏûÖÎ†• ÌïÑÎìú Ïä§ÌÉÄÏùº */
.place-search-input {
