  max-height: 200px; /* üî• ÎÑàÎ¨¥ Ïª§ÏßÄÏßÄ ÏïäÎèÑÎ°ù ÎÜíÏù¥ Ï†úÌïú */
  overflow-y: auto; /* ‚úÖ Ïä§ÌÅ¨Î°§ Í∞ÄÎä•ÌïòÎèÑÎ°ù ÏÑ§Ï†ï */
  background: white;
}

/* üìå Í∞úÎ≥Ñ Ïû•ÏÜå ÏïÑÏù¥ÌÖú Ïä§ÌÉÄÏùº */
.place-search-item {
