}

/* üìå Î∞òÏùëÌòï ÎîîÏûêÏù∏ */
@media (max-width: 480px) {
  .place-search-container {
    max-width: 95%; /* üî• ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑúÎäî Îçî ÎÑìÍ≤å */
  }
}
/* ‚úÖ Ïû•ÏÜå Í≤ÄÏÉâ Î™®Îã¨ Î∞∞Í≤Ω (ÌÅ¥Î¶≠ÌïòÎ©¥ Îã´Ìûò) */
.place-search-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3); /* üî• Î∞∞Í≤Ω Ïñ¥Îë°Í≤å */
  display: flex;
