html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  height: 100vh; /* ÏµúÏÜå ÎÜíÏù¥Î•º 100vhÎ°ú ÏÑ§Ï†ïÌïòÏó¨ ÎäòÏñ¥ÎÇ† Ïàò ÏûàÎèÑÎ°ù Ìï® */
  overflow: auto !important;


}

.main-page {
  display: flex;
  gap: 10px;
  background-color: #f3f4f6;
  overflow: hidden;
  height: calc(100vh - 57px); /* ÏõêÌïòÎäî Í≥ÑÏÇ∞ ÎÜíÏù¥ Ïú†ÏßÄ */
  align-items: stretch;
  width: 100%;  /* Í∞ÄÎ°úÌè≠ÏùÑ 100%Î°ú ÏßÄÏ†ï */
  height: 60%; /* ÏµúÏÜå ÎÜíÏù¥Î•º ÏÑ§Ï†ïÌïòÏó¨ ÎÇ¥Ïö©Ïóê Îî∞Îùº ÎäòÏñ¥ÎÇòÎèÑÎ°ù Ìï® */
  overflow-y: auto; /* ÏÑ∏Î°ú Ïä§ÌÅ¨Î°§ Í∞ÄÎä• */
  width: 100%;  /* Í∞ÄÎ°úÌè≠ÏùÑ 100%Î°ú ÏßÄÏ†ï */
  overflow: visible !important; /* üö® ÎÇ¥Î∂Ä ÏöîÏÜå ÌôïÏû• ÌóàÏö© */


}


.sidebar-container {
  width: 300px;
  flex-shrink: 0; /* Prevents it from shrinking further */
  /* border-radius: 12px; */
  padding: 10px;

}

.content {
  display: flex;
  flex-direction: column; /* Ensures children are stacked vertically */
  flex-grow: 1; /* Allows it to take available space */
  background-color: #c0d5efb0;
  gap: 20px;
  padding: 80px 80px 0px 80px;
  /* min-height: 100vh; Ensures it expands at least to the full viewport height */
  height: auto !important; /* üö® ÏûêÎèô ÌôïÏû• */
  overflow: visible !important; /* üö® ÎÇ¥Î∂Ä ÏöîÏÜåÍ∞Ä ÎÑòÏ≥êÎèÑ Ïä§ÌÅ¨Î°§ ÏóÜÏù¥ ÌôïÏû• */
}

.top-row {
  display: flex;
  flex-direction: row; /* .project-listÏôÄ .sidebarÎ•º Í∞ÄÎ°úÎ°ú Î∞∞Ïπò */
  gap: 20px; /* Îëê ÏöîÏÜå ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
}

/* ÌïÑÏöîÏóê Îî∞Îùº Í∞Å ÏöîÏÜåÏùò ÎÑàÎπÑÎ•º Ï°∞Ï†ï */
