/* Îã§Ìòú Ï∂îÍ∞Ä */
/* ÌîÑÎ°úÌïÑ ÏïÑÏù¥ÏΩò Ïä§ÌÉÄÏùº */
.profile-container {
  display: flex;
  align-items: center;
  position: relative;
}

.profile-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  border: 2px solid #ddd;
  transition: 0.3s;
}

.profile-icon:hover {
  border-color: #007bff;
}

.navbar-menu a,
.profile-link span {
  text-decoration: none;
  color: #70a0f4;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
}

/* ÌîÑÎ°úÌïÑ ÎìúÎ°≠Îã§Ïö¥ */
.profile-link {
  position: relative;
}

/* ÎìúÎ°≠Îã§Ïö¥ Î©îÎâ¥ Ïä§ÌÉÄÏùº */
.profile-dropdown {
  position: absolute;
  right: 0;
  top: 60px; /* Í∏∞Ï°¥ 50pxÏóêÏÑú ÏïΩÍ∞Ñ ÏïÑÎûòÎ°ú Ïù¥Îèô */
  width: 200px; /* ÎìúÎ°≠Îã§Ïö¥ ÎÑàÎπÑ ÌôïÏû• */
  background: white;
  border: 1px solid #ddd;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  z-index: 1000;
  padding: 5px 0; /* ÎÇ¥Î∂Ä ÏïÑÏù¥ÌÖú Í∞ÑÍ≤© Ï°∞Ï†ï */
}

/* ÎìúÎ°≠Îã§Ïö¥ ÏÉÅÎã®: ÌîÑÎ°úÌïÑ ÏÇ¨ÏßÑÍ≥º Ïú†Ï†ÄÎÑ§ÏûÑ Ï†ïÎ†¨ */
.dropdown-header {
  display: flex;
  align-items: center;
  padding: 12px;
  border-bottom: 1px solid #ddd;
  background-color: #f8f9fa;
}

.dropdown-profile-image {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.dropdown-username {
  font-size: 14px;
  font-weight: bold;
  color: #333;
}

/* ÎìúÎ°≠Îã§Ïö¥ ÎÇ¥Î∂Ä ÏïÑÏù¥ÌÖú Ïä§ÌÉÄÏùº */
.dropdown-item {
  display: flex;
  align-items: center;
  gap: 10px; /* ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ Í∞ÑÍ≤© */
  padding: 12px;
  text-decoration: none;
  color: #333 !important;
  transition: background 0.2s;
}

.dropdown-item:hover {
  background: #f1f1f1;
}

/* ÏïÑÏù¥ÏΩò Ïä§ÌÉÄÏùº */
.icon {
  font-size: 18px;
  color: #555;
}

/* Î°úÍ∑∏ÏïÑÏõÉ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.logout-btn {
  width: 100%;
  background: none;
  border: none;
  cursor: pointer;
  color: red !important;
  display: flex;
  align-items: center;
  gap: 10px;
}

.logout-btn:hover {
  background: #ffecec;
}


/* Í∏∞Ï°¥ Ïä§ÌÉÄÏùºÏóêÏÑú ÌÅ¨Í∏∞ Í¥ÄÎ†® Í∞í Ï°∞Ï†ï */
.navbar-menu a,
.profile-link span {
  position: relative;
  padding: 8px 15px;       /* ÎÇ¥Î∂Ä Ïó¨Î∞±ÏùÑ Ï§ÑÏûÑ */
  text-align: center;
  font-size: 14px;        /* Í∏ÄÏî® ÌÅ¨Í∏∞ÎèÑ ÏûëÍ≤å */
  font-weight: 500;
  color: #4b5f82;
  border-radius: 8px;
  background: transparent;
  z-index: 1;
  transition: background 0.3s, color 0.3s;

}

.navbar-menu a::before,
.profile-link span::before {
  content: "";
  position: absolute;
  inset: 0;
  padding: 2px; /* border ÎëêÍªò */
  border-radius: inherit;
  background: linear-gradient(135deg, #70a0f4, #b889fa);
  z-index: -1;
  /* ÎÇ¥Î∂Ä ÏòÅÏó≠ÏùÑ ÎßàÏä§ÌÅ¨Î°ú ÏûòÎùºÎÇ¥Ïñ¥ borderÎßå ÎÖ∏Ï∂ú */
  -webkit-mask:
    linear-gradient(#fff 0 0) content-box,
    linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
  mask-composite: exclude;
}

.navbar-menu a:hover,
.profile-link span:hover {
  color: white;
  background: linear-gradient(135deg, #70a0f4, #b889fa);
}


/* ÌîÑÎ°úÌïÑ ÎìúÎ°≠Îã§Ïö¥ (Ï†ÑÏ≤¥ Ïª®ÌÖåÏù¥ÎÑà) */
.profile-dropdown {
  display: flex;
  flex-direction: column; /* ÎÇ¥Î∂Ä ÏöîÏÜåÎì§ÏùÑ ÏÑ∏Î°ú Ï†ïÎ†¨ */
  width: 200px; /* ÎìúÎ°≠Îã§Ïö¥ ÌÅ¨Í∏∞ */
  padding: 5px 0;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* ÌîÑÎ°úÌïÑ ÏÑ§Ï†ï Î∞è ÎÇ¥ ÌîÑÎ°úÌïÑ Î≤ÑÌäº (ÎìúÎ°≠Îã§Ïö¥ ÎÇ¥Î∂Ä ÏöîÏÜå) */
.profile_setting,
.my_profile {
  display: flex; /* ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ Ï†ïÎ†¨ */
  align-items: center; /* ÏàòÏßÅ Ï†ïÎ†¨ */
  width: 100%; /* profile-dropdownÍ≥º Í∞ôÏùÄ ÎÑàÎπÑ Ïú†ÏßÄ */
  padding: 12px;
  font-size: 14px;
  font-weight: 500;
  color: #4b5f82;
  border-radius: 8px;
  background: transparent;
  text-decoration: none;
  transition: background 0.3s, color 0.3s;
  text-align: left; /* ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ ÏôºÏ™Ω Ï†ïÎ†¨ */
  position: relative;
  z-index: 1;
}

/* Í∏∞Ï°¥ Í∑∏ÎùºÎç∞Ïù¥ÏÖò Î≥¥Îçî Ïä§ÌÉÄÏùº Ï†úÍ±∞ */
.profile_setting::before,
.my_profile::before {
  content: none !important;
  display: none !important;
}

/* ÏïÑÏù¥ÏΩò Ïä§ÌÉÄÏùº */
.profile_setting .icon,
.my_profile .icon {
  font-size: 18px;
  margin-right: 8px; /* ÏïÑÏù¥ÏΩòÍ≥º ÌÖçÏä§Ìä∏ Í∞ÑÍ≤© */
}

/* ÎßàÏö∞Ïä§ Ìò∏Î≤Ñ Ìö®Í≥º */
.profile_setting:hover,
.my_profile:hover {
  background: linear-gradient(135deg, #70a0f4, #b889fa);
  color: white;
}

.profile_setting:hover .icon,
.my_profile:hover .icon {
  color: white;
}
.navbar-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px; /* Î©îÎâ¥ Í∞ÑÍ≤© Ï°∞Ï†ï */
  padding: 10px;
  border-radius: 10px; /* Îë•Í∑º Î™®ÏÑúÎ¶¨ */
  /* background-color: white; */
  min-width: 400px; /* Ï†ÑÏ≤¥ Î©îÎâ¥ ÎÑàÎπÑ ÏµúÏÜåÍ∞í ÏÑ§Ï†ï */
}

/* Î©îÎâ¥ ÎÇ¥Î∂Ä Ìï≠Î™©(ÎåÄÏãúÎ≥¥Îìú, ÌÜµÍ≥Ñ, ÌåÄÏõê) */
.navbar-menu > a {
  flex: 1; /* ÎèôÏùºÌïú ÌÅ¨Í∏∞ Ïú†ÏßÄ */
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 90px; /* ÏµúÏÜå ÎÑàÎπÑ ÏÑ§Ï†ï */
  height: 50px; /* ÎèôÏùºÌïú ÎÜíÏù¥ ÏÑ§Ï†ï */
  text-align: center;
  padding: 12px 15px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 8px;
  transition: background 0.3s, color 0.3s;
  text-decoration: none;
  color: #4b5f82;
  border: 1px solid #ddd; /* Í∞úÎ≥Ñ Ìï≠Î™© ÌÖåÎëêÎ¶¨ Ï∂îÍ∞Ä */
}

/* ÌîÑÎ°úÌïÑ Î©îÎâ¥ Î≤ÑÌäº (ÌîÑÎ°úÌïÑ ÏûêÏ≤¥ ÌÅ¨Í∏∞ Ïú†ÏßÄ) */
.profile-link {
  flex: 1;
  min-width: 90px; /* ÌîÑÎ°úÌïÑ Î©îÎâ¥ ÌÅ¨Í∏∞ ÎèôÏùºÌïòÍ≤å */
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border: 1px solid #ddd;
  border-radius: 8px;
  transition: background 0.3s, color 0.3s;
  position: relative; /* ÎìúÎ°≠Îã§Ïö¥ ÏúÑÏπò Ï°∞Ï†ï */
}

/* ÌîÑÎ°úÌïÑ ÌÖçÏä§Ìä∏ (ÌîÑÎ°úÌïÑ ÏòÅÏó≠ ÌÅ¥Î¶≠ Í∞ÄÎä•ÌïòÍ≤å) */
.profile-link > span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

/* ÌîÑÎ°úÌïÑ ÎìúÎ°≠Îã§Ïö¥ (ÌîÑÎ°úÌïÑ ÎÇ¥Î∂Ä ÏöîÏÜå Î≥ÄÍ≤Ω Î∞©ÏßÄ) */
.profile-dropdown {
  position: absolute;
  top: 60px; /* ÌîÑÎ°úÌïÑ Î≤ÑÌäº ÏïÑÎûòÎ°ú ÏúÑÏπò */
  right: 0;
  width: 200px;
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  padding: 5px 0;
  z-index: 10;
}

/* ÌîÑÎ°úÌïÑ ÎìúÎ°≠Îã§Ïö¥ ÎÇ¥Î∂Ä ÏöîÏÜå Î≥ÄÍ≤Ω Î∞©ÏßÄ */
.profile-dropdown span,
.profile-dropdown a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 10px;
  text-align: left;
  font-size: 14px;
  font-weight: 400;
  color: #333;
  border: none;
  width: auto;
  height: auto;
}

.profile-dropdown.active {
  display: block; /* active ÌÅ¥ÎûòÏä§Í∞Ä Î∂ôÏúºÎ©¥ Î≥¥ÏûÑ */
}

/* ÌîÑÎ°úÌïÑ Î≤ÑÌäºÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑ Îïå ÎìúÎ°≠Îã§Ïö¥ Î≥¥Ïù¥Í∏∞ */
.profile-link:hover .profile-dropdown {
