  /* üîπ ÌîÑÎ°úÌïÑ Ïù¥ÎØ∏ÏßÄÏôÄ Ïù¥Î¶Ñ ÏÇ¨Ïù¥ Í∞ÑÍ≤© ÏÑ§Ï†ï */
}

.username {
  font-weight: bold;
  font-size: 14px;
}

.timestamp {
  font-size: 12px;
  color: #777;
}

.pin-icon {
  font-size: 20px;
  color: #aaa;
}

.activity-content {
  max-height: 200px;
  /* 10Ï§Ñ Ï†ïÎèÑÏùò ÎÜíÏù¥ */
  overflow: hidden;
  position: relative;
  transition: max-height 0.3s ease-in-out;
}


.activity-content.expanded {
  max-height: none;
  /* ÌéºÏ≥§ÏùÑ Îïå Ï†ÑÏ≤¥ Î≥¥Ïù¥ÎèÑÎ°ù */
}

.activity-content.expanded::after {
  display: none;
}

.activity-content::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: rgba(255, 255, 255, 0);
  display: none;
  /* Í∏∞Î≥∏Ï†ÅÏúºÎ°ú Ïïà Î≥¥Ïù¥Í≤å ÏÑ§Ï†ï */
}

.activity-content.truncated::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background: rgba(255, 255, 255, 0);
  display: block;
}


.activity-content.has-image {
  max-height: none;
  /* Ïù¥ÎØ∏ÏßÄÍ∞Ä Ìè¨Ìï®Îêú Í≤ΩÏö∞ Ï†úÌïú ÏóÜÏùå */
}

.uploaded-image {
  max-width: 100%;
  height: auto;
  object-fit: contain;
  display: block;
}

/* ÍπîÎÅîÌïú "Ï†ÑÏ≤¥ Î≥¥Í∏∞" Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.toggle-expand-button {
  display: flex !important;
  /* üîπ Ìï≠ÏÉÅ Î≥¥Ïù¥ÎèÑÎ°ù Í∞ïÏ†ú */
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin: 5px auto;
  font-size: 18px;
  font-weight: bold;
  color: #888;
  border: none;
  background: none;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.toggle-expand-button:hover {
  color: #000;
  /* üîπ Ìò∏Î≤Ñ Ïãú ÏÉâÏÉÅ Í∞ïÏ°∞ */
  transform: scale(1.1);
}



.activity-content h3 {
