  align-items: center; /* ÏÑ∏Î°ú Ï§ëÏïô Ï†ïÎ†¨ */
  width: 100%;
  margin-top: 20px; /* Î≤ÑÌäºÍ≥º ÏúÑÏ™Ω ÏΩòÌÖêÏ∏† Í∞Ñ Ïó¨Î∞± */
}

.load-more-button {
